.modal {
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  transform: translateY(100%);
  background: rgba($color: #000, $alpha: 0.5);
  pointer-events: none;
  display: grid;
  place-items: center;
  overflow-y: auto;
  transition: opacity 0.3s ease 0s;

  &[aria-hidden="false"] {
    opacity: 1;
    z-index: 100;
    visibility: visible;
    transform: translateY(0);
    pointer-events: all;
  }
}

.modal__wrapper {
  max-width: 490px;
  min-height: 605px;
  padding: 30px 40px 36px;
  background: $primary-color;
  display: grid;
  align-items: start;
}

.modal__form {
  color: $color-white;
  display: grid;
  align-items: start;

  h2 {
    font-size: 28px;
    line-height: 140%;
    margin-bottom: 13px;
  }

  p {
    margin: 0 0 31px;
    padding-right: 20px;
  }

  fieldset {
    grid-template-columns: 1fr;
    gap: 31px;
    margin: 0 0 30px;
  }

  textarea {
    height: 104px;
    margin: 0 0 31px;
    padding-left: 23px;
  }

  input:not([type="checkbox"]) {
    height: 51px;
    padding: 0 23px;
  }

  button[type="submit"] {
    margin-bottom: 0;
    height: 47px;
  }
}
